.tasksList__taskButton {
    border: 0;
    color: rgb(255, 255, 255);
    width: 30px;
    height: 30px;
    padding: 0;
    background: #1a7b1e;
}

.task-list__button:disabled {
  transition: 0.2s;
  color: #666;         
  cursor: not-allowed;    
  opacity: 0.7;
}    

.tasksList__taskButton--remove {
    border: 0;
    color: rgb(255, 255, 255);
    width: 30px;
    height: 30px;
    padding: 0;
    background: #ff0000;
}

.tasksList__taskButton--done {
    background: #1a7b1e;
}

.task-list__buttons {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
  }

  .task-list__button {
    background: none;
    border: none;
    color: #00796b;
    font-size: 16px;
    cursor: pointer;
    padding: 4px 6px;
  }
